import { Helmet } from 'react-helmet-async'
import { useParams } from 'react-router'

import { Container, Grid, type Theme, Typography, useMediaQuery, useTheme } from '@mui/material'

import PageTitleWrapper from 'src/components/PageTitleWrapper'

import PageHeader from 'src/content/manage/PageHeader'

import { useGetClubAdminReviews, useHideReview, useShowReview } from 'src/hooks/api/review'
import useSafeAuthUser from 'src/hooks/useSafeAuthUser'

import AdminReviewCard from './AdminReviewCard'

const Review: React.FC = () => {
  const { id } = useParams()
  const clubId = Number(id)

  const { userInfo } = useSafeAuthUser()
  const club = userInfo?.clubs.find(c => c.id === clubId)

  const { data: reviews } = useGetClubAdminReviews(clubId)
  const hideReviewMutation = useHideReview(clubId)
  const showReviewMutation = useShowReview(clubId)

  const handleToggleVisibility = (reviewId: number, isHiddenByClub: boolean) => {
    if (isHiddenByClub) {
      showReviewMutation.mutate({ reviewId })
    } else {
      hideReviewMutation.mutate({ reviewId })
    }
  }

  const isUpperThanSm = useMediaQuery((theme: Theme) => theme.breakpoints.up('sm'))

  return (
    <>
      <Helmet>
        <title>후기 관리</title>
      </Helmet>
      <PageTitleWrapper>
        <PageHeader clubName={club?.name ?? ''} />
      </PageTitleWrapper>
      <Container maxWidth="lg" sx={{ px: isUpperThanSm ? 2 : 0 }}>
        <Grid container spacing={3}>
          {reviews?.length ? (
            reviews.map(review => (
              <Grid item xs={12} key={review.id}>
                <AdminReviewCard {...review} onToggleVisibility={handleToggleVisibility} />
              </Grid>
            ))
          ) : (
            <Grid item xs={12}>
              <Typography align="center" variant="body1" color="text.secondary" sx={{ py: 4 }}>
                등록된 후기가 없습니다.
              </Typography>
            </Grid>
          )}
        </Grid>
      </Container>
    </>
  )
}

export default Review
