import { useState } from 'react'

import CheckCircleIcon from '@mui/icons-material/CheckCircle'
import RadioButtonUncheckedIcon from '@mui/icons-material/RadioButtonUnchecked'
import { Box, Card, Divider, Menu, MenuItem, Stack, Typography } from '@mui/material'

import SemesterChip from 'src/components/SemesterChip'

import { type ClubReview } from 'src/responses/review'

interface AdminReviewCardProps extends ClubReview {
  onToggleVisibility: (reviewId: number, isHiddenByClub: boolean) => void
}

const AdminReviewCard: React.FC<AdminReviewCardProps> = review => {
  const {
    id,
    name,
    description,
    year,
    semester,
    content,
    keywords,
    recommendedPerson,
    userNickname,
    isHiddenByClub,
    onToggleVisibility
  } = review

  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null)
  const open = Boolean(anchorEl)

  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget)
  }

  const handleClose = () => {
    setAnchorEl(null)
  }

  const handleSelect = (hiddenByClub: boolean) => {
    if (isHiddenByClub !== hiddenByClub) {
      onToggleVisibility(id, hiddenByClub)
    }
    handleClose()
  }

  return (
    <Card
      sx={{
        p: '34px 36px 13px',
        borderRadius: '10px',
        boxShadow: '0px 2px 2px 0px rgba(159, 162, 191, 0.25), 0px 9px 16px 0px rgba(159, 162, 191, 0.18)',
        display: 'flex',
        flexDirection: 'column',
        gap: '14px'
      }}
    >
      <Stack direction="row" justifyContent="space-between" alignItems="flex-start">
        <Stack spacing="3px">
          {name && (
            <Typography
              sx={{
                fontFamily: 'Pretendard',
                fontSize: '13px',
                fontWeight: 400,
                color: '#000000',
                lineHeight: 1.2
              }}
            >
              {name}
            </Typography>
          )}
          {description && (
            <Typography
              sx={{
                fontFamily: 'Pretendard',
                fontSize: '14px',
                fontWeight: 400,
                color: 'rgba(34, 51, 84, 0.7)',
                lineHeight: 1.2
              }}
            >
              {description}
            </Typography>
          )}
        </Stack>
        <SemesterChip year={year} semester={semester} />
      </Stack>

      <Typography
        sx={{
          fontFamily: 'Pretendard',
          fontSize: '13px',
          lineHeight: '1.5',
          color: '#000000'
        }}
      >
        {content}
      </Typography>

      {keywords && keywords.length > 0 && (
        <Stack spacing="3px">
          {keywords.map(keyword => (
            <Stack
              key={keyword.id}
              direction="row"
              alignItems="center"
              spacing="10px"
              maxWidth="232px"
              minHeight="41px"
              sx={{
                border: '1px solid #CCCCCC',
                borderRadius: '10px',
                padding: '4px 8px'
              }}
            >
              <Typography
                sx={{
                  fontSize: '13px',
                  lineHeight: 1,
                  minWidth: '34px',
                  textAlign: 'center'
                }}
              >
                {keyword.icon}
              </Typography>
              <Typography
                sx={{
                  fontFamily: 'Pretendard',
                  fontSize: '13px',
                  color: '#000000',
                  lineHeight: 1
                }}
              >
                {keyword.label}
              </Typography>
            </Stack>
          ))}
        </Stack>
      )}

      <Stack direction="row" justifyContent="space-between" alignItems="center" sx={{ pt: 0.5 }}>
        <Typography
          sx={{
            fontFamily: 'Pretendard',
            fontSize: '13px',
            color: '#FF9393'
          }}
        >
          {recommendedPerson ? `# ${recommendedPerson}에게 추천!` : ''}
        </Typography>
        <Typography
          sx={{
            fontFamily: 'Pretendard',
            fontSize: '14px',
            color: '#B1B1B1'
          }}
        >
          from. {userNickname}
        </Typography>
      </Stack>

      <Divider sx={{ borderColor: 'rgba(159, 162, 191, 0.25)' }} />

      <Stack direction="row" justifyContent="space-between" alignItems="center" minHeight="25.5px">
        <Box>
          {isHiddenByClub === true && (
            <Box
              sx={{
                backgroundColor: '#B1B1B1',
                borderRadius: '16px',
                padding: '3px 7px',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center'
              }}
            >
              <Typography
                sx={{
                  fontFamily: 'Pretendard',
                  fontSize: '13px',
                  fontWeight: 700,
                  color: '#FFFFFF'
                }}
              >
                비공개
              </Typography>
            </Box>
          )}
        </Box>
        <Typography
          onClick={handleClick}
          sx={{
            fontFamily: 'Pretendard',
            fontSize: '13px',
            fontWeight: 700,
            color: '#5569FF',
            cursor: 'pointer'
          }}
        >
          공개 상태 변경하기
        </Typography>
        <Menu
          anchorEl={anchorEl}
          open={open}
          onClose={handleClose}
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'right'
          }}
          transformOrigin={{
            vertical: 'top',
            horizontal: 'right'
          }}
        >
          <MenuItem onClick={() => handleSelect(true)}>
            <Stack direction="row" spacing={1} alignItems="center">
              {isHiddenByClub === true ? <CheckCircleIcon sx={{ color: '#5569FF' }} /> : <RadioButtonUncheckedIcon />}
              <Typography>비공개</Typography>
            </Stack>
          </MenuItem>
          <MenuItem onClick={() => handleSelect(false)}>
            <Stack direction="row" spacing={1} alignItems="center">
              {isHiddenByClub !== true ? <CheckCircleIcon sx={{ color: '#5569FF' }} /> : <RadioButtonUncheckedIcon />}
              <Typography>공개</Typography>
            </Stack>
          </MenuItem>
        </Menu>
      </Stack>
    </Card>
  )
}

export default AdminReviewCard
