import { Chip, useTheme } from '@mui/material'

import { type DateProps } from 'src/models/date'

const SemesterChip: React.FC<DateProps> = date => {
  const theme = useTheme()

  const yearLabel = date.year.toString().slice(2)
  const semesterLabel = date.semester === 'SPRING' ? '1' : '2'

  return (
    <Chip
      label={`${yearLabel}-${semesterLabel}`}
      size="small"
      sx={{ borderRadius: '16px', fontSize: theme.typography.body2.fontSize, fontWeight: 500 }}
    />
  )
}

export default SemesterChip
